/* --- NAVIGATION BAR --- */

.navigation-bar {
    height: 60px;
    width: 100%;

    display: flex;
    align-items: center;

    position: relative;
    padding: 0;
    background-color: rgb(251, 250, 250);
    color: black;
}

.navigation-logo {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: auto;
}

.navigation-menu {
    margin-left: auto;
    margin-right: auto;
}

.navigation-menu ul {
    display: flex;
    padding: 0;
    margin: 0;
}

.navigation-menu li {
    list-style-type: none;
    margin: 0 1rem;
}

.navigation-menu li a {
    color: rgb(123, 123, 123);
    text-decoration: none;
    display: block;
    width: 100%;
}

.navigation-menu li a:hover {
    color: rgb(193, 72, 67);
    font-weight: bold;
}

.hamburger {
    display: none;

    position: absolute;
    top: 50%;
    right: 5px;
    transform: translateY(-50%);

    height: 50px;
    width: 50px;
    border: 0;
    border-radius: 0%;
    padding: 0.5rem;
    background-color: black;
    filter: opacity(80%);

    cursor: pointer;
    transition: background-color 0.1s ease-in-out;
}

.hamburger:hover {
    background-color: rgb(193, 72, 67);
}



/* --- MOBILE VIEW --- */

@media screen and (max-width: 730px) {
    .navigation-menu.expanded ul {
        display: block;
    }

    .navigation-menu ul {
        display: none;

        position: absolute;
        top: 60px;
        left: 0;
        flex-direction: column;
        width: 100%;
        /* height: calc(100vh - 77px); */
        background-color: hsl(0 0% 100% / 0.975);
        border-top: 1px solid black;
        border-bottom: 1px solid black;
    }

    @supports (backdrop-filter: blur(1.5rem)) {
        .navigation-menu ul {
            background-color: hsl(0 0% 100% / 0.1);
            backdrop-filter: blur(1.5rem);
        }
    }

    .navigation-menu li {
        text-align: center;
        margin: 0; 
    }

    .navigation-menu li a {
        color: black;
        font-size: 3vh;
        width: 100%;
        padding: 5vh 0;
    }
    
    .navigation-menu li a:hover {
        background-color: hsl(0, 0%, 75%);
        font-weight: bold;
    }

    .hamburger {
        display: block;
    }
}