/* --- LOGO IMAGE --- */

.logo-image img {
    display: block;
    width: 100%;
    height: calc(20vh + 20vw);
    object-fit: cover;
    object-position: 50% 27.5%;
}



/* --- SOCIAL MEDIA ICONS --- */

header .social-media-links ul {
    display: flex;

    position: absolute;
    right: 5px;
    top: calc(20vh + 20vw + 15px);

    padding: 0;
    margin: 0;
}

header .social-media-links li {
    list-style-type: none;
    margin: 0.5rem;
}

header .social-media-links img {
    width: 25px;
    height: 25px;
    padding: 10%;
    border-radius: 50%;
    filter: opacity(30%);
}

header .social-media-links img:hover {
    filter: opacity(100%);
}



/* --- MOBILE VIEW --- */

@media screen and (max-width: 730px) {

    header .social-media-links.expanded {
        display: none;
    }

    header .social-media-links ul {
        flex-direction: column;
        top: 60px;
    }
    
    header .social-media-links img {
        width: calc(50px - 0.5rem);
        height: calc(50px - 0.5rem);
        filter: opacity(70%);
    }
}